import { FC } from 'react';
import { IconSmallTriangleDown } from '@douyinfe/semi-icons';
import { Select } from '@douyinfe/semi-ui';
import { SelectProps } from '@douyinfe/semi-ui/lib/es/select';
import styles from './index.module.scss';

interface ITriggerSelectProps extends SelectProps {
  value?: any;
  onChange?: (v: any) => void;
}
export const TriggerSelect: FC<ITriggerSelectProps> = ({
  value,
  onChange,
  ...props
}) => (
  <Select
    {...props}
    borderless={false}
    placeholder="请选择"
    style={{
      height: 20,
      background: 'rgba(255, 255, 255, 0.1)',
      border: 'unset',
      maxWidth: '160px',
      ...(props.style || {}),
    }}
    className={styles['trigger-select']}
    value={value}
    onChange={v => {
      onChange?.(v);
    }}
    arrowIcon={
      <IconSmallTriangleDown style={{ fontSize: 12, color: '#fff' }} />
    }
  />
);
