import { useMemo } from 'react';
import { Typography } from '@douyinfe/semi-ui';
import styles from './index.module.scss';
import { TableItem } from '@/assets/json/data';
import { measureTextWidth } from '@/utils';

const tableCellPadding = 50;

interface Props {
  tableInfo: TableItem;
  dimension?: string[];
}

const useColumns = (props: Props) => {
  const { tableInfo, dimension = [] } = props;
  const firstRowScores = tableInfo.data[0].Scores;

  // 合并所有行的数据集名字
  const column = useMemo(
    () =>
      Object.keys(firstRowScores)
        // .sort()
        .filter(keyName => dimension.includes(keyName))
        .map(keyName => ({
          title: keyName,
          dataIndex: keyName,
          children: Object.keys(firstRowScores[keyName])
            // .sort()
            .map(childKeyName => ({
              title: childKeyName,
              dataIndex: [keyName, childKeyName].join('.'),
              width: measureTextWidth(childKeyName) + tableCellPadding,
              sorter: (aItem?: any, bItem?: any) => {
                if (aItem === undefined || bItem === undefined) {
                  return 0;
                }
                return Number(aItem[keyName][childKeyName]) -
                  Number(bItem[keyName][childKeyName]) >
                  0
                  ? 1
                  : -1;
              },
              render(val: number) {
                return Number(val) ? Number(val).toFixed(3) : '-';
              },
            })),
        })),
    [tableInfo, dimension],
  );

  const rtn = [
    {
      title: 'Models',
      dataIndex: 'name',
      width: 200,
      fixed: true,
      sorter: (aItem?: any, bItem?: any) => {
        if (aItem === undefined || bItem === undefined) {
          return 0;
        }

        return String(aItem.ModelDisplayName).localeCompare(
          String(bItem.ModelDisplayName),
        );
      },
      render: (value: string) => (
        <div className={styles['list-item-content-title']}>
          <Typography.Text
            ellipsis={{
              showTooltip: true,
              rows: 3,
            }}
            style={{
              wordBreak: 'break-all',
            }}
          >
            {value}
          </Typography.Text>
        </div>
      ),
    },
    ...column,
  ];

  return rtn;
};

export default useColumns;
