/* eslint-disable max-lines-per-function */
import { TableDataItem } from '@/assets/json/data';
import { EChartsOption } from 'echarts-for-react';
import { uniq } from 'lodash-es';
import { useMemo } from 'react';

const useBarOption = (
  data?: TableDataItem[],
  dimension?: string,
): EChartsOption => {
  /** 当前维度下的所有父级维度名 */
  const dimensionsName = useMemo(() => {
    const rtn: string[] = [];
    data?.forEach(item => {
      const scores = item.Scores;

      Object.keys(scores).forEach(parentDimension => {
        rtn.push(parentDimension);
      });
    });

    return uniq(rtn).sort((a, b) => a.localeCompare(b));
  }, [data, dimension]);

  /** 定义每个维度名字 */
  const indicators = dimensionsName.sort((a, b) => a.localeCompare(b));

  const modelNameScore: { [modelType: string]: number[] } = {};

  const config = {
    rotate: 90,
    align: 'left',
    verticalAlign: 'middle',
    position: 'insideBottom',
    distance: 15,
  };

  const labelOption = {
    show: true,
    position: config.position,
    distance: config.distance,
    align: config.align,
    verticalAlign: config.verticalAlign,
    rotate: config.rotate,
    formatter: '{c}',
    fontSize: 12,
    rich: {
      name: {},
    },
  };

  Object.values(data || {}).forEach(dataSetItem => {
    const modelName = dataSetItem.Method || dataSetItem.Model || '';
    // // 依据子维度字典序排序
    modelNameScore[modelName] = dimensionsName.map(parentDimension =>
      Number(
        dataSetItem.Scores[parentDimension]?.[dimension || '']?.toFixed(2),
      ),
    );
  });

  // 每一条条形图有多少子条形图
  const childNumsPerBar = Object.values(modelNameScore || {})?.length;

  const barData = Object.entries(modelNameScore).map(
    ([modelName, scoreList]) => ({
      name: modelName,
      type: 'bar',
      barGap: 0,
      label: labelOption,
      emphasis: {
        focus: 'series',
      },
      data: scoreList,
      // itemStyle: { color: modelNameColorList?.[modelName as any] },
    }),
  );

  const option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow',
      },
    },
    legend: {
      data: Object.keys(modelNameScore),
      bottom: '0',
      type: 'scroll',
      x: 'center',
    },
    toolbox: {
      show: true,
      orient: 'vertical',
      left: 'right',
      top: 'center',
      feature: {
        mark: { show: true },
        dataView: { show: true, readOnly: false },
        magicType: { show: true, type: ['line', 'bar', 'stack'] },
        restore: { show: true },
        saveAsImage: { show: true },
      },
    },
    xAxis: [
      {
        type: 'category',
        axisTick: { show: false },
        data: indicators,
        axisLabel: {
          show: true,
          interval: 0,
          width: 50 * childNumsPerBar,
          overflow: 'breakAll',
        },
      },
    ],
    yAxis: [
      {
        type: 'value',
      },
    ],
    dataZoom: [
      {
        // 默认控制x轴
        type: 'slider', // 图标下方的伸缩条
        show: true, // 是否显示
        xAxisIndex: [0], // 控制x轴，数组可以同时控制多个轴
        realtime: true,
        start: 0, // 伸缩条开始位置
        end: 100, // 伸缩条结束位置
        height: 23,
        bottom: 30,
        borderColor: 'transparent',
        backgroundColor: '#f5f5f5',
        fillerColor: 'transparent',
        brushSelect: false,
      },
      {
        type: 'inside', // 滚动条内置在坐标系中
        xAxisIndex: [0],
      },
    ],
    grid: {
      left: 30,
      right: 30,
      bottom: 80,
    },
    series: barData,
  };

  return option;
};

export default useBarOption;
