/* eslint-disable max-lines-per-function */
import { TableDataItem } from '@/assets/json/data';
import { EChartsOption } from 'echarts-for-react';
import { uniq } from 'lodash-es';
import { useMemo } from 'react';

const useRadarOption = (
  data?: TableDataItem[],
  dimension?: string,
): EChartsOption => {
  /** 当前维度下的所有子维度名 */
  const dimensionsName = useMemo(() => {
    const rtn: string[] = [];
    data?.forEach(item => {
      const scores = item.Scores;

      Object.keys(scores).forEach(parentDimension => {
        rtn.push(parentDimension);
      });
    });

    return uniq(rtn);
  }, [data, dimension]);

  const legendData = useMemo(
    () =>
      data
        ?.sort((aItem, bItem) =>
          (aItem.Method || aItem.Model || '').localeCompare(
            bItem.Method || bItem.Model || '',
          ),
        )
        .map(item => item.Method || item.Model || ''),
    [data],
  );

  // /** 雷达图指标，定义每个维度的最大值和名字 */
  const indicators = useMemo(() => {
    const maxCache: { [parentDimension: string]: number } = {};

    data?.forEach(item => {
      const scores = item.Scores;

      Object.keys(scores).forEach(parentDimension => {
        if (
          (scores[parentDimension][dimension || ''] || 0) >=
          (maxCache[parentDimension] || 0)
        ) {
          const currScore = scores[parentDimension][dimension || ''] || 1;
          // * 0.1 是为了保留一点边距
          maxCache[parentDimension] = currScore + currScore * 0.2;
        }
      });
    });

    const rtn = Object.keys(maxCache)
      .sort((a, b) => a.localeCompare(b))
      .map(parentDimension => ({
        name: parentDimension,
        max: maxCache[parentDimension] || 1,
      }));

    return rtn;
  }, [data, dimension]);

  const radarData = useMemo(
    () =>
      data
        ?.sort((aItem, bItem) =>
          (aItem.Method || aItem.Model || '').localeCompare(
            bItem.Method || bItem.Model || '',
          ),
        )
        .map(item => {
          const modelName = item.Method || item.Model || '';

          return {
            name: modelName,
            value: Object.keys(item.Scores)
              .sort()
              .map(key => item.Scores[key][dimension || ''] || 0),
          };
        }),
    [data, dimension],
  );

  if (indicators.length === 0) {
    return {};
  }

  return {
    /** 图例展示 */
    legend: {
      data: legendData,
      bottom: '0',
      type: 'scroll',
      left: '10%',
    },
    tooltip: {
      trigger: 'item',
      position: 'right',
      // confine: true,
      formatter(params: { name: string; value: string }) {
        const html = dimensionsName
          .map(
            (name, index) => `
              <tr>
                <td>${name}:</td>
                <td>${params.value[index]}</td>
              </tr>
            `,
          )
          .join('');

        return `
          <div>
            <h4>${params.name}</h4>
            <table>
                ${html}
            </table>
          </div>
        `;
      },
    },
    grid: {
      top: 20,
    },
    radar: {
      center: ['50%', '55%'],
      indicator: indicators,
      name: {
        formatter(value: string) {
          return value.replace(/(.{15})/g, '$1\n');
        },
      },
      axisLabel: {
        width: 50, // 设置标签最大宽度为 100 像素
        overflow: 'truncate', // 超出部分使用省略号显示
        // overflow: 'break', // 超出部分自动换行
        align: 'left', // 设置标签对齐方式为左对齐
        verticalAlign: 'middle', // 设置标签垂直对齐方式为居中
      },
      nameGap: 15,
    },
    series: [
      {
        name: 'compare',
        type: 'radar',
        tooltip: {
          trigger: 'item',
        },
        areaStyle: {},
        data: radarData,
      },
    ],
  };
};

export default useRadarOption;
