import styles from './index.module.scss';
import { Space, Tag, Typography } from '@douyinfe/semi-ui';
import cx from 'classnames';
import Echarts from 'echarts-for-react';
import { useEffect, useMemo, useRef, useState } from 'react';
import useRadarOption from './radarOption';
import useBarOption from './barOptions';
import Model from '../model';
import { get } from 'lodash-es';
import { TableItem, rankData } from '@/assets/json/data';

const DimensionsTable = () => {
  const { modalState } = Model.useContext();
  const [currDimension, setCurrDimension] = useState('');
  const [chartHeight, setChartHeight] = useState(450);
  const chartRef = useRef(null);

  const currData = useMemo(
    () =>
      (
        get(rankData, modalState.rankTypeKeyPath) as unknown as TableItem
      )?.data?.filter(item => {
        const modelName = item.Method || item.Model || '';
        // 如果没有选择模型，默认展示所有模型数据
        return modalState.models.length === 0
          ? true
          : modalState.models.includes(modelName);
      }),
    [modalState.rankTypeKeyPath, modalState.models],
  );

  const dimensionList = useMemo(() => {
    const firstScores = Object.values(currData[0]?.Scores || {});
    const firstScoreDimensions = Object.keys(firstScores[0] || {});

    setCurrDimension(firstScoreDimensions[0]);

    return firstScoreDimensions;
  }, [currData]);

  useEffect(() => {
    if (chartRef.current) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-expect-error
      const height = chartRef.current.offsetHeight; // 获取高度
      setChartHeight(height);
    }
  }, [Boolean(chartRef.current)]); // 依赖项为空数组，只在组件挂载后执行一次

  // 雷达图数据
  const radarOption = useRadarOption(currData, currDimension);

  // 条形图数据
  const barOption = useBarOption(currData, currDimension);

  return (
    <div className={styles.dimensionsTable}>
      <div className={styles.title}>
        <Typography.Title heading={6} style={{ margin: '10px 0' }}>
          Evaluating metrics
        </Typography.Title>
        <Space className={styles.tagContainer} spacing={3}>
          {dimensionList?.map((dimension, idx) => {
            const isSelected = currDimension === dimension;
            return (
              <Tag
                key={`TagBtn_${idx}`}
                className={cx(styles.label, {
                  [styles.selected]: isSelected,
                })}
                onClick={() => {
                  setCurrDimension(dimension);
                }}
              >
                <div className={styles.labelName}>{dimension}</div>
              </Tag>
            );
          })}
        </Space>
      </div>
      <div
        ref={chartRef}
        className={cx(styles.chartContainer, 'transparent-scrollbar')}
      >
        <Space className={styles.echarts} spacing={20}>
          <div className={styles.chart}>
            <Echarts
              key={JSON.stringify(radarOption || {})}
              option={radarOption}
              opts={{ width: 'auto', height: chartHeight - 30 }}
            />
          </div>
          <div className={styles.chart} style={{ flex: 1, minWidth: 714 }}>
            <Echarts
              key={JSON.stringify(barOption || {})}
              option={barOption}
              opts={{ width: 'auto', height: chartHeight - 30 }}
            />
          </div>
        </Space>
      </div>
    </div>
  );
};

export default DimensionsTable;
