import { Space, Table, TreeSelect, Typography } from '@douyinfe/semi-ui';
import { useEffect, useMemo, useRef, useState } from 'react';
import styles from './index.module.scss';
import useColumns from './useColumns';
import { TriggerSelect } from '@/components/common/TriggerSelect';
import { OptionProps } from '@douyinfe/semi-ui/lib/es/select';
import { TableItem, rankData } from '@/assets/json/data';
import { useSearchParams } from '@edenx/runtime/router';
import cx from 'classnames';
import { IconSmallTriangleDown } from '@douyinfe/semi-icons';
import { TreeNodeData } from '@douyinfe/semi-ui/lib/es/tree';
import { get } from 'lodash-es';
import Model from '../model';

interface RankData {
  [rankTypeKey: string]: {
    name: string;
    tables: {
      [tableKey: string]: {
        name: string;
        data: Record<string, any>[];
      };
    };
  };
}

export interface CellData extends Record<string, number | string> {
  modelDisplayName: string;
  score: number;
}

const DataTable = () => {
  const { modalState, setModalState } = Model.useContext();
  const [dimension, setDimension] = useState<string[]>();
  const tableRef = useRef(null);
  const [searchParam] = useSearchParams();
  const rankParam = searchParam.get('rank') || '';
  const modelParam = searchParam.get('models') || '';

  const modelList = useMemo(() => {
    const tableItem: TableItem = get(
      rankData,
      modalState.rankTypeKeyPath,
    ) as unknown as TableItem;

    return tableItem.data.map(item => {
      const modelName = item.Method || item.Model || '';

      return modelName;
    });
  }, [rankData, modalState.rankTypeKeyPath]);

  const rankTreeOptions = useMemo(() => {
    const rtn: TreeNodeData[] = [];

    Object.keys(rankData as RankData).map(typeKey => {
      rtn.push({
        label: rankData[typeKey].name,
        value: typeKey,
        key: `${typeKey}.tables.default`,
        children:
          Object.keys(rankData[typeKey].tables).length > 1
            ? Object.entries(rankData[typeKey].tables).map(
                ([subTypeKey, tableItem]) => ({
                  label: tableItem.name,
                  value: `${typeKey}.${subTypeKey}`,
                  key: `${typeKey}.tables.${subTypeKey}`,
                }),
              )
            : undefined,
      });
    });

    return rtn;
  }, [rankData]);

  const modelOptions = useMemo(
    () =>
      modelList.map(modelName => ({
        label: modelName,
        value: modelName,
      })),
    [modelList],
  );

  const dimensionOptions = useMemo(() => {
    let dimensionList: string[] = [];
    const tableItem: TableItem = get(
      rankData,
      modalState.rankTypeKeyPath,
    ) as unknown as TableItem;

    dimensionList = Object.keys(tableItem.data[0].Scores);

    setDimension(dimensionList);

    return dimensionList.map(item => ({
      label: item,
      value: item,
    }));
  }, [rankData, modalState.rankTypeKeyPath]);

  useEffect(() => {
    if (rankParam) {
      const [typeKey, subTypeKey = 'default'] = rankParam.split('.');

      setModalState({
        rankType: rankParam,
        rankTypeKeyPath: `${typeKey}.tables.${subTypeKey}`,
        models: modelParam ? (modelParam.split('~') as string[]) : modelList,
      });
    }
  }, [rankParam, modelParam, modelList]);

  const columns = useColumns({
    tableInfo: get(
      rankData,
      modalState.rankTypeKeyPath,
    ) as unknown as TableItem,
    dimension,
  });

  const dataSource = useMemo(() => {
    const tableItem: TableItem = get(
      rankData,
      modalState.rankTypeKeyPath,
    ) as unknown as TableItem;

    return tableItem.data
      .filter(item => {
        const modelName = item.Method || item.Model || '';
        // 如果没有选择模型，默认展示所有模型数据
        return modalState.models.length === 0
          ? true
          : modalState.models.includes(modelName);
      })
      .map(item => ({
        name: item.Method || item.Model,
        ...item.Scores,
      }));
  }, [rankData, modalState.rankTypeKeyPath, modalState.models]);

  return (
    <div ref={tableRef} className={styles.dataTable}>
      <div className={styles.title}>
        <Space>
          <Typography.Title heading={6} style={{ margin: '10px 0' }}>
            Data details
          </Typography.Title>
          <div className={cx(styles.filter, styles['filter-rank'])}>
            <div className={styles['filter-item']}>
              <div className={styles.text}>Lists</div>
              <TreeSelect
                className={styles['tree-select']}
                size="small"
                value={modalState.rankType}
                expandAll={true}
                arrowIcon={
                  <IconSmallTriangleDown
                    style={{ fontSize: 12, color: '#fff' }}
                  />
                }
                onChange={(value: any, node: any) => {
                  if (Object.keys(rankData[value]?.tables || {}).length <= 1) {
                    console.log('changeRank:', value, node.key);
                    searchParam.set(
                      'rank',
                      node.key.replace('.tables', '').replace('.default', ''),
                    );
                    searchParam.delete('models');

                    const urlObj = new URL(location.href);

                    urlObj.hash = urlObj.hash.replace(
                      /(:?\?).+$/,
                      `?${searchParam.toString()}`,
                    );

                    window.location.href = urlObj.toString();
                  }
                }}
                treeData={rankTreeOptions}
                dropdownClassName={styles['tree-select-dropdown']}
              />
            </div>
          </div>
          <div className={styles.filter}>
            <div className={styles['filter-item']}>
              <div className={styles.text}>Models</div>
              <TriggerSelect
                value={modalState.models}
                onChange={(value: any) => {
                  if (value.length === 0) {
                    searchParam.delete('models');
                  } else {
                    searchParam.set('models', value.join('~'));
                  }

                  const urlObj = new URL(location.href);

                  urlObj.hash = urlObj.hash.replace(
                    /(:?\?).+$/,
                    `?${searchParam.toString()}`,
                  );

                  window.location.href = urlObj.toString();
                }}
                multiple={true}
                optionList={modelOptions as OptionProps[]}
                maxTagCount={2}
                showRestTagsPopover={true}
                restTagsPopoverProps={{
                  position: 'top',
                  className: styles['rest-tags-popover'],
                }}
                style={{ maxWidth: 'auto' }}
              />
            </div>
          </div>
          <div className={styles.filter}>
            <div className={styles['filter-item']}>
              <div className={styles.text}>Dimension</div>
              <TriggerSelect
                value={dimension}
                onChange={(value: any) => {
                  setDimension(value);
                }}
                multiple={true}
                optionList={dimensionOptions as OptionProps[]}
                maxTagCount={2}
                showRestTagsPopover={true}
                restTagsPopoverProps={{
                  position: 'top',
                  className: styles['rest-tags-popover'],
                }}
                style={{ maxWidth: 'auto' }}
              />
            </div>
          </div>
        </Space>
      </div>
      <div className={styles['table-container']}>
        <Table
          key={`${JSON.stringify(JSON.stringify(columns))}_${
            modalState.rankTypeKeyPath
          }`}
          scroll={{ x: '100%', y: 450 }}
          columns={columns}
          dataSource={dataSource}
          loading={false}
          pagination={false}
          bordered={true}
        />
      </div>
    </div>
  );
};

export default DataTable;
