import { createModel, useSetState } from '@byted/hooks';

const Model = createModel(() => {
  const [modalState, setModalState] = useSetState<{
    rankType: string;
    rankTypeKeyPath: string;
    models: string[];
  }>({
    rankType: 'overall',
    rankTypeKeyPath: 'match.tables.overall',
    models: [],
  });

  return {
    modalState,
    setModalState,
  };
});

export default Model;
